//vuex配置

//定义属性/(数据)

var state={
	isCollapsed:false,
    flag:true,
    i:0,
    loginS:true
}

//定义getters 
var getters={
	rotateIcon(state){
		return [
            'menu-icon',
            state.isCollapsed ? 'rotate-icon' : ''
           ]
	},
	flag(state){
		return state.flag;
	},
	isCollapsed(state){
		return state.isCollapsed;
	},
	menuitemClasses(state){
		 return [
            'menu-item',
            state.isCollapsed ? 'collapsed-menu':''
            ]
	},
	icoI(state){
		 return state.i;
	},
	loginS(state){
		 return state.loginS;
	}
}


//定义Actions(方法),要执行的操作，流程判断，异步请求

const actions={
	collapsedSider({commit,state}){
		commit('incre');//提交一个名为incer的变化,名字可自定义
	},
	editLogin({commit,state}){
		commit('editLogin');
	}
}

//处理状态的变化，定义mutations。
const mutations={
	incre(state){
		state.i++;
      state.flag=!state.flag;
	},
	editLogin(state){
		state.loginS=false;
	},
	updateMessage(state,value){
		state.isCollapsed=value;
	}
}


//导出store对象

export default {
  state,
  getters,
  actions,
  mutations
}


