<template>
	 <div>
        	 <Affix>
            	<div class="layout-header-bar layout-logo-menu demo-affix">
            		<img v-if="flag" src="../assets/logo.png" alt="" />
            		<h1  v-if="!flag">iView</h1>
            	</div>
        	 </Affix>

            
            <Menu   v-show="flag" theme="dark" active-name="1-0" style="margin-top:15px;"  width="auto" :class="menuitemClasses">
                <Submenu name="1">
                		<template slot="title">
                        <Icon type="ios-paper"></Icon>
                        	内容管理
	                    </template>
	                    <MenuItem name="1-1"><Icon type="ios-copy-outline"></Icon>文章管理</MenuItem>
	                    <MenuItem name="1-2"><Icon type="ios-email"></Icon>评论管理</MenuItem>
	                    <MenuItem name="1-3"><Icon type="gear-a"></Icon>举报管理</MenuItem>    
                </Submenu>
                <Submenu name="2">
                    <template slot="title">
                        <Icon type="ios-people"></Icon>
                        	用户管理
                    </template>
                    <MenuItem name="2-1"><Icon type="person-add"></Icon>新增用户</MenuItem>
                    <MenuItem name="2-2"><Icon type="person-stalker"></Icon>活跃用户</MenuItem>
                </Submenu>
                <Submenu name="3">
                    <template slot="title">
                        <Icon type="stats-bars"></Icon>
                        	统计分析 
                    </template>
                        <MenuItem name="3-1"><Icon type="power"></Icon>新增和启动</MenuItem>
                        <MenuItem name="3-2"><Icon type="ios-pie-outline"></Icon>活跃分析</MenuItem>
                        <MenuItem name="3-3"><Icon type="ios-alarm"></Icon>时段分析</MenuItem>
                </Submenu>
                
            </Menu>
           
             <Menu  v-show="!flag" active-name="4-1" theme="dark"  style="margin-top:15px;"  width="auto" :class="menuitemClasses">
                     <Poptip placement="right" width="200" trigger="hover" class="item-button">
                     	<Button type="ghost" class="ivu-btn ivu-btn-text">
                     		<Icon class="i-vue" type="ios-paper"></Icon>
                     	</Button>
                     	 	<ul class="api ivu-dropdown-menu" style="width:100%;"  slot="content">
                     	 		<li class="ivu-dropdown-item">
                     	 			<Icon type="gear-a"></Icon>
                     	 			&nbsp;&nbsp;&nbsp;权限管理
                     	 		</li>
                     	 		
                     	 	</ul>
                     </Poptip>
                     <Poptip placement="right" width="200" trigger="hover" class="item-button">
                     	<Button type="ghost"  class="ivu-btn ivu-btn-text">
                     		<Icon class="i-vue"  type="ios-people"></Icon>
                     	</Button>
                     	 <div class="api" slot="content">
                     	 	<ul class="api ivu-dropdown-menu" style="width:100%;"  slot="content">
                     	 		<li class="ivu-dropdown-item">
                     	 			<Icon type="person-add"></Icon>
                     	 			&nbsp;&nbsp;&nbsp;新增管理
                     	 		</li>
                     	 		<li class="ivu-dropdown-item">
                     	 			<Icon type="person-stalker"></Icon>
                     	 			&nbsp;&nbsp;&nbsp;活跃用户
                     	 		</li>
                     	 	</ul>
                     	 	</div>
                     </Poptip>
                     
                     <Poptip placement="right" width="200" trigger="hover" class="item-button">
                     	<Button type="ghost"  class="ivu-btn ivu-btn-text">
                     		<Icon class="i-vue"  type="stats-bars"></Icon>
                     	</Button>
                     	 <div class="api" slot="content">
                     	 	<ul class="api ivu-dropdown-menu" style="width:100%;"  slot="content">
                     	 		<li class="ivu-dropdown-item">
                     	 			<Icon type="power"></Icon>
                     	 			&nbsp;&nbsp;&nbsp;启动
                     	 		</li>
                     	 		<li class="ivu-dropdown-item">
                     	 			<Icon type="ios-pie-outline"></Icon>
                     	 			&nbsp;&nbsp;&nbsp;活跃分析
                     	 		</li>
                     	 		<li class="ivu-dropdown-item">
                     	 			<Icon type="ios-alarm"></Icon>
                     	 			&nbsp;&nbsp;&nbsp;时段分析
                     	 		</li>
                     	 	</ul>
                     	 	</div>
                     </Poptip>
                 	<!--<MenuItem name="4-2"><Icon type="ios-people"></Icon></MenuItem>
                 	<MenuItem name="4-3"><Icon type="stats-bars"></Icon></MenuItem>-->
            </Menu>
    </div>
</template>

<script>
import  {mapGetters} from 'vuex'	
 export default {
 	data(){
 		return{
 			
 		}
 	},
 	computed:{
       ...mapGetters([
	 	'menuitemClasses','flag'
		])  
 	}
 }
</script>

<style lang="less" scoped>
.item-button{
	button{
		text-align:center; 
		width:78px; 
		padding: 10px 0px;
	}
	i.i-vue{
		color:#fff !important;
		margin-left:-15px;
	}
}
span.i-span-vue{
		color:#495060;
}
  .layout-logo-menu{
    	height:64px;
    	line-height:64px;
    	text-align: center;
    	color: #2d8cf0;
    }
  .layout-header-bar{
    background: #fff;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .menu-icon{
        transition: all .3s;
    }
  .menu-item span{
        display: inline-block;
        overflow: hidden;
        width: 69px;
        text-overflow: ellipsis;
        white-space: nowrap;
        vertical-align: bottom;
        transition: width .2s ease .2s;
    }
    .menu-item i{
        transform: translateX(0px);
        transition: font-size .2s ease, transform .2s ease;
        vertical-align: middle;
        font-size: 16px;
    }
    .collapsed-menu span{
        width: 0px;
        transition: width .2s ease;
    }
    .collapsed-menu i{
        transform: translateX(5px);
        transition: font-size .2s ease .2s, transform .2s ease .2s;
        vertical-align: middle;
        font-size: 22px;
    }
</style>


