<template>
	<div class="vue-bg">
		 <Card  id="Card" :dis-hover="false" >
        <div style="text-align:center">
            <img width="60" src="https://file.iviewui.com/dist/76ecb6e76d2c438065f90cd7f8fa7371.png">  
        </div>
         <Form ref="formInline" :model="formInline" :rules="ruleInline" >
	        <FormItem prop="user">
	            <Input type="text" v-model="formInline.user" placeholder="请输入用户名">
	                <Icon size="16" type="ios-person-outline" slot="prepend"></Icon>
	            </Input>
	        </FormItem>
	        <FormItem prop="password">
	            <Input type="password" v-model="formInline.password" placeholder="请输入密码">
	                <Icon size="16" type="ios-locked-outline" slot="prepend"></Icon>
	            </Input>
	        </FormItem>
	        <FormItem style="text-align: center;">
	            <Button type="primary" @click="handleSubmit('formInline')">登录</Button>
	        </FormItem>
	    </Form>
        </Card>
	</div>
</template>

<script>
import  {mapActions} from 'vuex'
	  export default {
        data () {
            return {
                formInline: {
                    user: '',
                    password: ''
                },
                ruleInline: {
                    user: [
                        { required: true, message: '用户名不能为空', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '密码不能为空', trigger: 'blur' },
                        { type: 'string', min: 6, message: '密码长度不能少于6位', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('登陆成功！即将跳转');
                        setTimeout(this.editLogin,2000)
                        
                    } else {
                        this.$Message.error('登陆失败');
                    }
                })
            },
            ...mapActions([
		 	'editLogin'
		 	])
        }
    }
</script>

<style scoped>
	#Card{
		width:320px;
		position: fixed;
		top:50%;
		left:50%;
		margin-top:-133.5px;
		margin-left:-160px;
		z-index: 2;
	}
	.vue-bg{
		width:100%;
		height: 100%;
		position: absolute;
		left:0;
		top:0;
		background: url(../assets/vuex.jpg) no-repeat center center;
		z-index: 1;
		
			
	}
</style>


