<template>
	<div class="layout">
		 <Layout>
	          <Sider ref="side1" hide-trigger collapsible :collapsed-width="78"  v-model="reverseMessage">
	          	<SiderMenu></SiderMenu>
	          </Sider>
	        <Layout>
		        <HeaderMenu></HeaderMenu>
	            <Content></Content>
	            <Footer style="text-align: center;">copy@2015-2018虚拟物云版权所有</Footer>
	        </Layout>
	    </Layout>
   </div>
</template>

<script>	
import  {mapGetters} from 'vuex'
	import HeaderMenu from './HeaderMenu.vue'
	import SiderMenu from './SiderMenu.vue'
	import Content from './Content.vue'
	 export default {
        data () {
            return {
            	
            }
        },
        components:{
        	HeaderMenu,SiderMenu,Content
        },
        computed:{
	      	reverseMessage:{
	      		get () {
			        if(this.icoI>0){
	 				 this.$refs.side1.toggleCollapse();
		 			}  
		 			return this.isCollapsed
			    },
			    set (value) {
			      this.$store.commit('updateMessage', value)
			    }
	      	},
	       ...mapGetters([
		 	'isCollapsed','icoI'//取到的事函数返回值
			])  
	 	}
    }
</script>

<style>
</style>


