<style lang="less" scoped>
    .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
    }
    .layout-header-bar{
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .rotate-icon{
        transform: rotate(-90deg);
    }
   
    .Floatr{
    	float: right;
    }
  
</style>
<template>
    <Header :style="{padding: 0}" class="layout-header-bar">
    	<Row>
    		   <Col span="2">
    		   	<Icon @click.native="collapsedSider" :class="rotateIcon" :style="{margin: '20px 20px 0'}" type="navicon-round" size="24"></Icon>
    		   </Col>
    		   <Col span="12">
    		   	 <Menu mode="horizontal"  :theme="theme1" :style="height" active-name="0">
    		   	 	  <MenuItem name="0">
			            <Icon type="ios-paper"></Icon>
			            	首页
			        </MenuItem>
			        <MenuItem name="1">
			            <Icon type="ios-paper"></Icon>
			            内容管理
			        </MenuItem>
			        <MenuItem name="2">
			            <Icon type="ios-people"></Icon>
			            用户管理
			        </MenuItem>
			        <Submenu name="3">
			            <template slot="title">
			                <Icon type="stats-bars"></Icon>
			                统计分析
			            </template>
			            <MenuGroup title="使用">
			                <MenuItem name="3-1">新增和启动</MenuItem>
			                <MenuItem name="3-2">活跃分析</MenuItem>
			                <MenuItem name="3-3">时段分析</MenuItem>
			            </MenuGroup>
			            <MenuGroup title="留存">
			                <MenuItem name="3-4">用户留存</MenuItem>
			                <MenuItem name="3-5">流失用户</MenuItem>
			            </MenuGroup>
			        </Submenu>
			        <MenuItem name="4">
			            <Icon type="settings"></Icon>
			            综合设置
			        </MenuItem>
			    </Menu>
    		   </Col>
    		   <Col span="10">
    		   		 <Menu mode="horizontal" theme="light" :style="height" active-name="0" class="Floatr">
			            <MenuItem name="1">
			                 <Icon type="ios-bell-outline" size="18"></Icon>
			            </MenuItem>
			            <MenuItem name="2">
			                <Icon type="ios-keypad"></Icon>
			            </MenuItem>
			            <MenuItem name="3">
			              <Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" />&nbsp;&nbsp;&nbsp;Admin&nbsp;<Icon type="chevron-down"></Icon>
			            </MenuItem>
			            <MenuItem name="4">
			               <Icon size="16" type="gear-b"></Icon>
			            </MenuItem>
				   </Menu>
    		   </Col>
    	</Row>
        
    </Header>
</template>
<script>
	import  {mapGetters,mapActions} from 'vuex'
    export default {
        data () {
            return {
                theme1: 'light',
                height:'height:64px;'
            }
        },
        computed:mapGetters([
	 	'rotateIcon'//取到的事函数返回值
		 ]),
		methods:mapActions([
		 	'collapsedSider'
		 ])
	    }
</script>


